// Variables
@import "variables";
.absolute{
  position: absolute;
}
.actions{
  right: 1em;
  top:10px;
}
.big-padding{
  padding:30px;
}
.blue-gray{
  background-color: #37474f;
}
.circle-shopping-cart{
  background-color:#232980;
  border-radius: 50%;
  padding:1em 1em; 
}
.creado
{
  background-color: green;
}
.floating
{
  position: fixed;
  bottom:2em;
  right:2em;
  z-index: 100;
}
.large-padding
{
  padding: 10px 15px !important;
}
.inline-block{
  display:inline-block !important;
}
.navbar{
  margin-bottom: 0px !important;
}
.no-margin{
  margin:0px !important;
}
.no-padding
{
  padding:0px !important;
}
.no-transform{
  text-transform: none !important;
}
.product{
  width: 600px !important;
  max-width: 100%;
  padding: 1em;
  position: relative;
  h1{
    padding-bottom: 10px;
    border-bottom:solid 1px
  }
}
.product-avatar
{
  max-width: 100%;
}
.products-container
{
  .product{
    margin-top:1em;
  }
}
.red-text
{
  color:#ff4d4d !important;
}
.sale-data{
  padding: 1em;
  text-align: center;
  border-right: solid 1px #222;
  span{
    display: block;
    font-size: 2.2em;
    color:#3f51b5;
  }
}
.tabla{
  background-color:white;
}
.top-space{
  margin-top: 1em;
}
.white{
  background-color: white;
}
.white-text{
  color:white;
}
/*animacion para el circulo*/
.highlight{
  -webkit-animation:highlight .5s;
  animation:highlight .5s;
}
@-webkit-keyframes highlight{
  50%{
    background-color:#ffff00;
    color:black;
  }
  100%{
    background-color:inherit;
  }
}
@keyframes highlight{
  50%{
    background-color:#ffff00;
    color:black;
  }
  100%{
    background-color:inherit;
  }
}